<template>
   <div>
      <section class="bg-white">
         <h2 class="text-3xl pt-6 text-blue 
         md:text-6xl
         lg:text-4xl">- {{product}} -</h2>

         <span class="font-Clarissa text-5xl">{{country}}</span>

         <!-- BUSCADOR -->
         <searcher></searcher>
      </section>

      <section class="bg-white text-white">
         <figure>
            <img src="@/assets/img/singlepole-top-blue.png" alt="" />
         </figure>
 
         <div class="bg-blue-180 pb-10">
            <!-- Listado polo -->
            <div class="flex flex-wrap justify-center gap-4 text-sm py-6 m-auto
            md:text-2xl md:w-4/6">
            <button v-for="(item, index) in poles" :key="index"
               :class="{'btn-active': (item.active)}"
               class="border-2 border-blue bg-white text-blue rounded-75 px-8
               hover:border-orange hover:bg-orange hover:text-white"
               @click="getPoleInfo(index)">{{item.name}}</button>
            </div>

            <!-- Listado de productos por polo -->
            <div class="container flex flex-col items-center py-10">
               <product-list-template
               v-for="(item, index) in product_list" :key="index"
               :item="item" :index="index" 
               :params="{
                  poleKey: country,
                  productKey: product
               }"
               ></product-list-template>             
            </div>
         </div>
      </section>
   </div>
</template>
<script>
import Searcher from "@/components/searcher/Searcher.vue";
import ProductListTemplate from '@/components/ProductListTemplate'

export default {
   name: 'Hotels',
   components: {
      Searcher,
      ProductListTemplate
   },
   data() {
      return {
         product: '',
         country: '',
         poles: [
            {name: 'Occidente de Cuba', active: false},
            {name: 'La Habana', active: false},
            {name: 'Varadero', active: true},
            {name: 'Centro de Cuba', active: false},
            {name: 'Cayo Santa Maria', active: false},
            {name: 'Cayo Coco y Cayo Guillermo', active: false},
            {name: 'Oriente de Cuba', active: false},
         ],
         product_list: [
            {
               img: require("@/assets/img/hotel_thumb/thumb1.png"),
               name: "Iberostar Laguna Azul",          
               keyword: "iberostar-lagunaazul",          
               price_from: '59.99',
               star: '59.99',
               segment: {
                  name: "playa",
                  img: require("@/assets/img/segment/playa.png")
               },       
            }
         ],
         pole: ''
      }
   },
   created() {
      this.pole = this.poles[0].name
      this.product = this.$route.params.productKey
      this.country = this.$route.params.poleKey
   },
   methods: {
      getPoleInfo(index) {
         this.poles.map((e, i) => {            
            e.active = (index == i) ? true : false
         })         
      }
   }
   
}
</script>
<style scoped>
   .btn-active{
      border-color: var(--orange) !important;
      -color: var(--orange) !important;
      color: white !important;
   }

   article {
      grid-template-rows: auto auto;
      grid-template-columns: auto;
   }

   @media screen and (min-width: 768px) {
      article {
         grid-template-columns: 27% auto;  
         grid-template-rows: auto;  
      }      
   }
</style>